<script>
	import {
		formatDate
	} from '@/common/date.js'
	import {
		mapMutations,
		mapState
	} from 'vuex'
    export default {
        data() {
            return {
				show:false,
				list:[],
				index: 0,
				rolename:'',
				occu:'',
				detail:true,
				giftList:[
					{
						name:"余晖",
						gift:[]
					},
					{
						name:"宓桃",
						gift:[]
					},
					{
						name:"武雪散",
						gift:[]
					},
					{
						name:"猿飞",
						gift:[]
					},
					{
						name:"哑头陀",
						gift:[]
					},
					{
						name:"岳琳",
						gift:[]
					}
				],
				user:[],
				currentPrice:'',
				giftid:'',
				showList1:true,
				showList2:true,
				showList3:true,
				showList4:true,
				showList5:true,
				showList6:true,
				carNumber:''
			}
        },
		filters: {
			formatDate(time) {
				let date = new Date(time);
				return formatDate(date, ' hh:mm:ss');
			}
		},
		computed: {
			...mapState('app', ['appName']),
			...mapState('user', ['userInfo']),
			...mapState('error', ['logs'])
		},
        onLoad() {
			this.getOrder()
			this.getUser()
		},
        methods: {
			getOrder() {
				const db = uniCloud.database()
				db.collection('boss').where({
					status:'1'
				}).get().then((res) => {
					this.list = res.result.data
				})
			},
			sale(e){
				this.carNumber = e
				this.detail = false
				this.getGift()
			},
			getGift(){
				this.$refs.priceshow.close()
				this.currentPrice = ''
				this.giftList[0].gift=[]
				this.giftList[1].gift=[]
				this.giftList[2].gift=[]
				this.giftList[3].gift=[]
				this.giftList[4].gift=[]
				this.giftList[5].gift=[]
				const db = uniCloud.database()
				db.collection('gift').where({
					number:this.carNumber
				}).get().then((res) => {
					
					const data=res.result.data;
					console.log(data)
					for(var i=0;i<data.length;i++){
						if(data[i].boss == '余晖'){
							this.giftList[0].gift.push(data[i])
						}else if(data[i].boss == '宓桃'){
							this.giftList[1].gift.push(data[i])
						}
						else if(data[i].boss == '武雪散'){
							this.giftList[2].gift.push(data[i])
						}
						else if(data[i].boss == '猿飞'){
							this.giftList[3].gift.push(data[i])
						}
						else if(data[i].boss == '哑头陀'){
							this.giftList[4].gift.push(data[i])
						}
						else if(data[i].boss == '岳琳'){
							this.giftList[5].gift.push(data[i])
						}
					}
				})
			},
			bindPickerChange: function(e) {
			            console.log('picker发送选择改变，携带值为', e.target.value)
			            this.index = e.target.value
			},
			addUser(){
				this.$refs.popup.open()
			},
			getUser(){
				this.user=[]
				const db = uniCloud.database()
				db.collection('roles').where({
					user:this.userInfo.username
				}).get().then((res) => {
					var data=res.result.data
					for(var i=0;i<data.length;i++){
						this.user.push(data[i].role)
					}
				})
			},
			addRole(){
				const db = uniCloud.database()
				
				db.collection('roles').add([{
					user:this.userInfo.username,
					role:this.rolename,
					occu:this.occu
				}]).then((res)=>{
					this.$refs.popup.close()
					this.rolename=''
					this.occu=''
					uni.showToast({
						title:'添加角色成功',
						icon:'success',
						duration:1000
					})
					this.getUser()
				})
			},
			sendPrice(e){
				if(this.user.length == 0){
					alert('请先建立角色')
					return
				}
				this.$refs.priceshow.open()
				this.giftid = e
				
			},
			addPrice(){
				
				const r= /^\+?[1-9][0-9]*$/;
				if(!r.test(this.currentPrice/1000)){
					uni.showToast({
						title:'必须为1000的倍数',
						icon:'none',
						duration:1000
					})
					this.currentPrice=''
					return
				}
				const db=uniCloud.database()
				db.collection("gift").where({
					_id:this.giftid
				}).get().then((res)=>{
					var data=res.result.data[0]
					if(data.winner == "无"){
						console.log(parseInt(data.price)+":"+this.currentPrice)
					if(parseInt(data.price)>this.currentPrice){
						uni.showToast({
							title:'出价失败,少于当前价格',
							icon:'none',
							duration:2000
						})
					}else{
						db.collection("gift").where({_id:this.giftid}).update({
							price:this.currentPrice,
							winner:this.user[this.index],
							time:this.getTime()
						}).then(			
								db.collection('record').add({
									account:this.userInfo.username,
									role:this.user[this.index],
									price:this.currentPrice,
									time:this.getTime()
								})			
						)
						uni.showToast({
							title:"出价成功",
							icon:'success'
						})
					}	
					}else{
					if(parseInt(data.price)>=this.currentPrice){
						uni.showToast({
							title:'出价失败,少于当前价格',
							icon:'none',
							duration:2000
						})
					}else{
						db.collection("gift").where({_id:this.giftid}).update({
							price:this.currentPrice,
							winner:this.user[this.index],
							time:this.getTime()
						}).then(
							db.collection('record').add({
								account:this.userInfo.username,
								role:this.user[this.index],
								price:this.currentPrice,
								time:this.getTime()
							})											
						)
						uni.showToast({
							title:"出价成功",
							icon:'success'
						})
					}
					}
					 this.getGift()
				})
				
			},
			closeList(e){
				if(e == 1){
					this.showList1 = false
				}else if (e == 2){
					this.showList2 = false
				}else if (e == 3){
					this.showList3 = false
				}else if (e == 4){
					this.showList4 = false
				}else if (e == 5){
					this.showList5 = false
				}else if (e == 6){
					this.showList6 = false
				}
			},
			refresh(){
				this.getGift()
			},
			getTime:function(){
			
			var date = new Date(),
			year = date.getFullYear(),
			month = date.getMonth() + 1,
			day = date.getDate(),
			hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
			minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
			second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			month >= 1 && month <= 9 ? (month = "0" + month) : "";
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			// year + '-' + month + '-' + day + ' ' +
			var timer =  hour + ':' + minute + ':' + second;
			return timer;
			},
		}
    }
</script>

